import React, { useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { Button, Paper } from '@material-ui/core';
import Snackbar from '@material-ui/core/Snackbar';
import { useNavigate } from 'react-router-dom';
import { collection, addDoc } from 'firebase/firestore';
import { firestore } from '../fireSetting';

export default function Form({ handleFormSubmit }) {
  const [firstname, setFirstName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isError, setErrorFlag] = useState(false);
  const [errorMsg, setErrorMsg] = useState('');
  const navigate = useNavigate();

  const storeData = async (data) => {
    try {
      const docRef = await addDoc(collection(firestore, '/users'), {
        name: data.name,
        email: data.email,
        password: data.password,
      });

      const docRef2 = await addDoc(collection(firestore, '/activeusers'), {
        email: data.email,
        state: 'offline',
      });
      console.log('User: ', docRef.id);
    } catch (e) {
      console.log('Error: ', e);
    }
  };

  const handleFirstName = (e) => {
    setErrorFlag(false);
    setFirstName(e.target.value);
  };

  const handleEmail = (e) => {
    setErrorFlag(false);
    setEmail(e.target.value);
  };
  const handlePassword = (e) => {
    setErrorFlag(false);
    setPassword(e.target.value);
  };

  const handleSubmit = (e) => {
    if (firstname === '') {
      setErrorFlag(true);
      setErrorMsg('First Name is required');
      return;
    }
    const alphaRegex = /^[A-Za-z ]+$/;
    const emailRegex =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    const passwordRegex =
      /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,}$/;

    if (!alphaRegex.test(firstname)) {
      setErrorFlag(true);
      setErrorMsg('First Name should only contains letters');
      return;
    }

    if (email === '') {
      setErrorFlag(true);
      setErrorMsg('Email is required');
      return;
    }

    if (!emailRegex.test(email)) {
      setErrorFlag(true);
      setErrorMsg('Email is not Valid');
      return;
    }

    if (password === '') {
      setErrorFlag(true);
      setErrorMsg('Password is required');
      return;
    }

    if (!passwordRegex.test(password)) {
      setErrorFlag(true);
      setErrorMsg(
        'Password should be alpha-numeric with special characters(>8)'
      );
      return;
    }

    const data = {};
    data['name'] = firstname;
    data['email'] = email;
    data['password'] = password;

    storeData(data);

    handleFormSubmit(data);

    navigate('/success');
  };

  return (
    <div>
      <Snackbar open={isError} autoHideDuration={6000} message={errorMsg} />
      <Paper>
        <h1> Registration Form</h1>
        <TextField
          onChange={handleFirstName}
          value={firstname}
          label={'Name'}
        />

        <TextField onChange={handleEmail} value={email} label={'Email'} />
        <TextField
          onChange={handlePassword}
          value={password}
          label={'Password'}
          type="password"
        />

        <Button onClick={handleSubmit} variant="contained" color={'primary'}>
          Submit
        </Button>
      </Paper>
    </div>
  );
}
